@media screen and (width >= 595px) {
  .container {
    width: 595px;
    max-width: 668px;
  }
}

@media screen and (width >= 1200px) {
  .container {
    width: 1110px;
    max-width: none;
  }
}

body {
  color: #fff;
  background-color: #000;
  font-family: Montserrat, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.5;
}

h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
}

img {
  width: 100%;
  max-width: 100%;
  height: auto;
  display: block;
}

input:focus {
  outline: none;
}

.hero, .hero-inputs {
  color: #050505;
  flex-direction: column;
  gap: 25px;
  display: flex;
}

.hero {
  background-color: #46393941;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 304px;
}

.hero-image {
  object-fit: cover;
  object-position: center bottom;
  z-index: 0;
  max-height: 364px;
  position: absolute;
  top: 0;
  left: 0;
}

.hero-title {
  color: #dc56c5;
  text-align: center;
  width: 212px;
  font-size: 18px;
  font-weight: 700;
}

.hero-inputs {
  z-index: 5;
  justify-content: center;
  gap: 20px;
  width: 85%;
  min-width: 190px;
  max-width: 270px;
  display: flex;
  position: relative;
}

#search-input, .input-countries {
  color: #f0f8ff;
  background-color: #ffffff31;
  border: none;
  border-radius: 20px;
  width: 100%;
  height: 40px;
  padding: 10px 30px;
  position: relative;
}

.dropdown-countries, .search-submit {
  position: absolute;
  top: 15px;
  right: 30px;
}

.countries-set, .search-form {
  position: relative;
}

.countries-block {
  color: #b9adad;
  z-index: 10;
  background-color: #2c2525;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
  width: 100%;
  max-height: 100px;
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
  overflow: auto;
  box-shadow: 0 4px 12px #e2d1d140;
}

@media screen and (width >= 1200px) {
  .countries-block {
    max-height: 270px;
  }
}

.countries-set.is-open .countries-block {
  z-index: 10;
  display: block;
}

.countries-set.is-open .input-countries {
  border-bottom: 1px solid #000;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.country-item {
  cursor: pointer;
  border-top: 1px solid #000;
  padding: 5px 20px;
}

.event-container {
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  gap: 40px 14px;
  display: grid;
}

@media screen and (width >= 595px) {
  .event-container {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (width >= 1200px) {
  .event-container {
    grid-template-columns: repeat(4, 1fr);
  }
}

.event-item {
  text-align: center;
  color: #fff;
  cursor: pointer;
  flex-direction: column;
  gap: 6px;
  width: 130px;
  transition: transform .25s;
  display: flex;
  position: relative;
}

.event-item:hover {
  transform: translateY(-4px);
}

.event-item img {
  object-fit: cover;
  border-radius: 50px 0;
  height: 160px;
}

.event-item:before {
  content: "";
  opacity: .3;
  pointer-events: none;
  border: 1px solid #dc56c5;
  border-radius: 50px 0;
  width: 110px;
  height: 100px;
  position: absolute;
  top: -9px;
  right: -10px;
}

@media screen and (width >= 595px) {
  .event-item {
    width: 180px;
  }

  .event-item img {
    height: 225px;
  }

  .event-item:before {
    width: 155px;
    height: 145px;
    top: -12px;
    right: -12px;
  }
}

.event-title {
  color: #dc56c5;
  font-size: 14px;
  font-weight: 700;
}

.event-location {
  opacity: .9;
  font-size: 12px;
  font-weight: 600;
}

.modal-backdrop {
  z-index: 999;
  pointer-events: auto;
  opacity: 1;
  visibility: visible;
  background: #0000009e;
  width: 100vw;
  height: 100vh;
  transition: opacity .3s cubic-bezier(.25, .46, .45, .94), visibility .3s cubic-bezier(.25, .46, .45, .94);
  position: fixed;
  inset: 0;
  overflow-y: auto;
}

.modal-backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  background: #fff;
  border-radius: 12px;
  width: 88%;
  min-width: 320px;
  max-width: 980px;
  padding: 48px 28px 36px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -48%);
  box-shadow: 0 8px 38px #0000006b;
}

.btn-modal-close {
  color: #fff;
  cursor: pointer;
  background: #222;
  border: none;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  font-size: 20px;
  transition: background-color .2s, transform .16s;
  display: flex;
  position: absolute;
  top: 15px;
  right: 15px;
}

.btn-modal-close:hover, .btn-modal-close:focus {
  background: #444;
  transform: scale(1.1);
}
